.stickyNav{
  position: -webkit-sticky;
  position: sticky;
  top: 0;
  margin-top:10px;
  margin-bottom: 20px;
  z-index:999999;
}

.dropdown {
  position: relative;
  display: inline-block;
}

.dropdown-content {
  display: none;
  position: absolute;
  min-width: 140px;
  /*box-shadow: 0px 8px 16px 0px rgba(0,0,0,0.2);*/
  padding: 5px 5px;
  z-index: 1;
}

.dropdown:hover .dropdown-content {
  display: block;
}